.growth-section {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: white;
  color: black;
  position: relative;
  border-bottom: 1px solid #dcdcdc;

  h1 {
    font-size: 38px;
  }

  .inner-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 15vh;
    row-gap: 10vh;

    .vertical-text {
      rotate: 90deg;
      font-size: 50px;
      font-weight: bold;
    }
  }


  .line-container6 {
    width: 5vw;
    height: 10vh;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    position: absolute;
    top: 0px;
  }

  .animated-line6 {
    width: 5vw;
    height: 0;
    background-color: black;
    animation: lineAnimation6 2s ease-in-out forwards;
    animation-timeline: scroll();
    animation-range-start: 80%;
    animation-range-end: 95%;
  }

  @keyframes lineAnimation6 {
    from {
      height: 0%;
    }
    to {
      height: 100%;
    }
  }


  .line-container5 {
    width: 5vw;
    height: 38vh;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    position: absolute;
    bottom: 0px;
  }

  .animated-line5 {
    width: 5vw;
    height: 0;
    background-color: black;
    animation: lineAnimation5 2s ease-in-out forwards;
    animation-timeline: scroll();
    animation-range-start: 80%;
    animation-range-end: 90%;
  }

  @keyframes lineAnimation5 {
    from {
      height: 0%;
    }
    to {
      height: 100%;
    }
  }

}



